

$header-font: 'Bigshot One', cursive;

.part1 {
	width: 100%;
	opacity: 1;
	display: none;
	.title {
		position: absolute;
		top: 25%;
		left: 20%;
		z-index: 6;
		animation: infinite 7s ease-in title-ani;

	p {
		font-family: $header-font;
		position: absolute;
		top: 65%;
		left: -23%;
		font-size: 3vh;
	}
	
	h1 {
		font-family: $header-font;
		transform: translate( -50%, -50%);
		font-size: 9vh;
	}


	.title-gretel {
			transform: translate( 0%, -80%);
			color: #1f3437;
		}

	.title-hansel {
			color: #3f817d;
		}

	}

	.sun {
		position: absolute;
		height: auto;
		width: auto;
		max-height: 50vh;
		max-width: 50vw;
		top: 13%;
		left: 50%;
		z-index: 1;
		animation: sun-rotate 20s infinite linear;
		transition: 1s;
	}

	.house {
		position: absolute;
		height: auto;
		width: auto;
		max-height: 21vh;
		max-width: 31vw;
		top: 40%;
		left: 10%;
		z-index: 5;
	}

	
	.sky{
		width: 100%;
		background-image: url(../img/sky.png);
		background-repeat: no-repeat;
		background-size: cover;

		h2 {
			position: absolute;
			top: 80%;
			left: 40%;
			font-family: $header-font;
			font-size: 4vh;
			z-index: 6;
		}

		.cloud1{
			position: absolute;
			height: auto;
			width: auto;
			max-width: 100vw;
			max-height: 100vh;
			top: -20%;
			left: -20%;
			animation: infinite 45s ease-in cloud1;
		}

		

		.cloud2 {
			position: absolute;
			height: auto;
			width: auto;
			max-width: 100vw;
			max-height: 100vh;
			top: -15%;
			left: 15%;
			animation: infinite 55s ease-in cloud2;
		}

	

		.cloud3{
			position: absolute;
			height: auto;
			width: auto;
			max-width: 100vw;
			max-height: 100vh;
			top: -30%;
			left: 50%;
			transform: rotate(185deg);
			animation: infinite 55s ease-in cloud2;
		}

	}
	.hills {

		.hill1 {
			position: absolute;
			height: auto;
			width: auto;
			max-width: 100vw;
			max-height: 111vh;
			left: 20%;
			top: 30%;
			z-index: 2;
			
		}

		.hill2 {
			position: absolute;
			height: auto;
			width: auto;
			max-width: 100vw; 
			max-height: 111vh;
			top: 35%;
			left: 0%;
			z-index: 3;
		
		}

		.hill3 {
			position: absolute;
			height: auto;
			width: auto;
			max-width: 100vw; 
			max-height: 111vw;
			top: 65%;
			left: -5%;
			z-index: 4;
		}

	}
}














// =================ANIMATIONS===============


@keyframes title-ani {
	0% {
		transform: scale(0);
		opacity: 0;
	}
	25% {
		opacity: .4;
	}
	100% {
		transform: scale(1);
		opacity: 1;
	}
}


@keyframes sun-rotate {
	from {
		transform: rotate(360deg);
	}

	to {
		transform: rotate(0deg);
	}
}

@keyframes cloud1 {
	from {
		transform: translateX(-650px);
	}
	to {
		transform: translateX(1100px);
	}
}


@keyframes cloud2 {
	from {
		transform: translateX(-1500px);
	}
	to {
		transform: translateX(1100px);
	}
}

@keyframes cloud3 {
	from {
		transform: translateX(-3000px);
	}
	to {
		transform: translateX(1100px);
	}
}





	
